import styled, { css, keyframes } from "styled-components";
import { MaterialCubicBezier } from "../../globalStyle";

function template(i) {
  return `
    &:nth-child(${i + 1}) {
      animation-delay: ${i * 100}ms;
      .process-wrapper {
        animation-delay: ${i * 100 + 300}ms;
      }
    }
  `;
}
function getAnimations() {
  let str = "";
  for (let index = 0; index < 10; index += 1) {
    str += template(index);
  }
  return str;
}

const skillItemAnimation = keyframes`
  from {
    transform: translateX(-10%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
`;

const clipPathAnimation = keyframes`
  from {
    transform: scaleX(0);
    opacity: 0;
  }
  to {
    transform: scaleX(1);
    opacity: 1;
  }
`;

export const SkillWrapper = styled.div`
  padding: 10rem 0;

  .skill-list {
    font-family: Montserrat, sans-serif;

    &.animate {
      .skill-item {
        animation: ${skillItemAnimation} 400ms ${MaterialCubicBezier} forwards;

        .process-wrapper {
          animation: ${clipPathAnimation} 1s ${MaterialCubicBezier} forwards;
        }
      }
    }

    .skill-item {
      font-size: 4rem;
      color: #999;
      font-weight: 100;
      opacity: 0;
      transition: all 400ms ${MaterialCubicBezier};

      @media screen and (max-width: 400px) {
        font-size: 1.5rem;
      }
      &:not(:last-child) {
        margin-bottom: 2rem;
      }

      &:hover {
        color: #3498db;

        .process-wrapper {
          .process {
            &:before {
              color: #0b3c60;
            }
          }
        }
      }

      ${getAnimations()}
      .process-wrapper {
        position: relative;
        height: 4px;
        width: 100%;
        opacity: 0;
        transform-origin: left;

        .process {
          background: #333;
          height: 100%;
          position: relative;

          &:before {
            content: attr(data-percentage) "%";
            position: absolute;
            right: 0;
            bottom: 0;
            font-size: 2rem;
            font-weight: 600;
            color: #313131;
            transition: all 400ms ${MaterialCubicBezier};
            @media screen and (max-width: 400px) {
              font-size: 1rem;
              bottom: 0.5rem;
            }
          }
        }
      }
    }
  }
`;
