import React, { useContext, useEffect, useRef } from "react";
import { WorkWrapper } from "./Styled";
import { CommonTitle, Wrapper } from "../../globalStyle";
import { IntroWrapper } from "../Intro/Styled";
import { TweenLite } from "gsap";
import AppContext from "../../AppContext";

const WORK_HISTORY = [
  {
    name: "Merquri",
    url: "https://merquri.io",
    image: require("../../images/work/merquri.png").default
  },
  {
    name: "Bond Capital",
    url: "https://bond.sg",
    image: require("../../images/work/bond.png").default
  },
  {
    name: "Aspirasi",
    url: "https://aspirasi.co",
    image: require("../../images/work/aspirasi.png").default
  },
  {
    name: "Pacific Light",
    url: "https://pacificlight.com.sg",
    image: require("../../images/work/pacificlight.png").default
  },
  {
    name: "Allianz Adventure",
    url: "https://www.allianzadventure.com",
    image: require("../../images/work/allianz.png").default
  },
];

const Work = () => {
  const appContext = useContext(AppContext);
  const bgRef = React.useRef();

  const bindScrollEvent = (e) => {
    const scrollingFactor = window.innerHeight > 1080 ? 0.02 : 0.1;
    TweenLite.to(bgRef.current, 0.3, {
      backgroundPositionY: -window.pageYOffset * scrollingFactor,
    });
  };

  useEffect(() => {
    window.addEventListener("scroll", bindScrollEvent);
    return () => {
      window.removeEventListener("scroll", bindScrollEvent);
    };
  }, [bgRef]);

  return (
    <WorkWrapper>
      <div className="work-bg" ref={bgRef} data-scroll data-scroll-speed="2" />
      <Wrapper>
        <CommonTitle>Works</CommonTitle>
        <div className="work-list">
          {WORK_HISTORY.map((item, index) => {
            const { name, url } = item;
            return (
              <a
                href={url}
                target="_blank"
                className="work-item"
                key={name}
                onMouseEnter={() => appContext.setCurrentHover(item)}
                onMouseOut={() => appContext.setCurrentHover(undefined)}
                data-scroll
                data-scroll-speed={index + 0.5}
              >
                <h3>{name}</h3>
              </a>
            );
          })}
        </div>
      </Wrapper>
    </WorkWrapper>
  );
};

export default Work;
