import React, { useCallback, useEffect, useState } from "react";
import GlobalStyle, {AppContainer, WorkPreview} from "./globalStyle";
import { TweenLite } from "gsap";
import LocomotiveScroll from "locomotive-scroll";
import "locomotive-scroll/dist/locomotive-scroll.css";

import Intro from "./components/Intro";
import Work from "./components/Work";
import Skill from "./components/Skills";
import {
  BigTitle,
  Cursor,
  InnerCursor,
  IntroWrapper,
} from "./components/Intro/Styled";
import AppContext from "./AppContext";
import { WorkWrapper } from "./components/Work/Styled";
import Footer from "./components/Footer";

function App() {
  const appRef = React.useRef();
  const cursor = React.useRef();
  const innerCursor = React.useRef();
  const bigTitleRef = React.useRef();
  const workPreviewRef = React.useRef();

  const [currentHover, setCurrentHover] = useState();
  const [animateIntro, setAnimateIntro] = useState();
  const [animateSkills, setAnimateSkills] = useState();

  const bindMouseMove = useCallback(
    (e) => {
      const { clientX, clientY } = e;

      TweenLite.to(cursor.current, 0.3, {
        x: clientX - 20,
        y: clientY - 20,
      });

      TweenLite.to(innerCursor.current, 0.4, {
        x: clientX - 12,
        y: clientY - 12,
      });

      if (currentHover) {
        TweenLite.to(workPreviewRef.current, 0.4, {
          scale: 1,
          opacity: 1,
          x: clientX + 120,
          y: clientY - 300,
        });
      } else {
        TweenLite.to(workPreviewRef.current, 0.4, {
          scale: 0,
          opacity: 0,
        });
      }
    },
    [currentHover]
  );

  useEffect(() => {
    window.addEventListener("mousemove", bindMouseMove);
    return () => window.removeEventListener("mousemove", bindMouseMove);
  }, []);

  const bindScrollEvent = (e) => {
    TweenLite.to(bigTitleRef.current, 1, {
      y: -window.pageYOffset,
    });
  };

  useEffect(() => {
    window.addEventListener("scroll", bindScrollEvent);
    window.addEventListener("resize", bindScrollEvent);
    return () => {
      window.removeEventListener("scroll", bindScrollEvent);
      window.removeEventListener("resize", bindScrollEvent);
    };
  }, []);

  useEffect(() => {
    window.addEventListener("mousemove", bindMouseMove);
    return () => window.removeEventListener("mousemove", bindMouseMove);
  }, [currentHover]);

  useEffect(() => {
    const scroll = new LocomotiveScroll({
      el: appRef.current,
      smooth: true,
    });

    scroll.on("call", (func) => {
      if (func === "animateIntro") setAnimateIntro(true);
      if (func === "animateSkills") setAnimateSkills(true);
    });
  }, []);

  return (
    <React.Suspense fallback={<div>Loading</div>}>
      <AppContext.Provider
        value={{
          setCurrentHover: setCurrentHover,
          animateIntro: animateIntro,
          animateSkills: animateSkills,
        }}
      >
        <AppContainer>
          <GlobalStyle />
          <Cursor ref={cursor} />
          <InnerCursor ref={innerCursor} />
          <BigTitle ref={bigTitleRef}>
            Creativity is just connecting things. When you ask creative people
            how they did something, they feel a little guilty because they
            didn’t really do it, the just saw something. It seemed obvious to
            them after a while.
          </BigTitle>
          <WorkPreview
            ref={workPreviewRef}
            style={{ backgroundImage: `url(${currentHover?.image})` }}
          >
            {/*<div className="work-content">{currentHover?.name}</div>*/}
          </WorkPreview>
          <div className="body-container" ref={appRef}>
            <Intro />
            <Work />
            <Skill />
            <Footer />
          </div>
        </AppContainer>
      </AppContext.Provider>
    </React.Suspense>
  );
}

export default App;
