import React, { useCallback, useEffect } from "react";
import {
  BigTitle,
  Cursor,
  InnerCursor,
  IntroWrapper,
  MarqueeWrapper,
} from "./Styled";
import { CommonTitle, Wrapper } from "../../globalStyle";
import { TweenLite, Elastic } from "gsap";
import Marquee from "react-simple-marquee";
import AppContext from "../../AppContext";
import IsMobile from "../Util/IsMobile";

const Intro = () => {
  const squareRef = React.useRef();
  const circleRef = React.useRef();
  const rectangleRef = React.useRef();
  const animateInterval = React.useRef();

  const appContext = React.useContext(AppContext);

  const animateShapes = () => {
    const random = Math.random();
    const random2 = Math.random();
    const random3 = Math.random();
    const scalingFactor1 = IsMobile() ? 50 : 100
    const scalingFactor2 = IsMobile() ? 60 : 200

    TweenLite.to(squareRef.current, 1, {
      x: random * scalingFactor1,
      y: random * scalingFactor1,
      scale: random < 0.5 ? 0.5 : random,
      rotate: random * 360,
      borderRadius: random * 50,
      ease: Elastic.easeOut.config(0.7, 0.3),
    });

    TweenLite.to(circleRef.current, 2, {
      x: random2 * scalingFactor2,
      y: random2 * scalingFactor2,
      scale: random2 < 0.5 ? 0.5 : random2,
      ease: Elastic.easeOut.config(0.7, 0.3),
    });

    TweenLite.to(rectangleRef.current, 3, {
      x: random3 * scalingFactor1,
      y: random3 * scalingFactor1,
      scale: random3 < 0.5 ? 0.5 : random3,
      borderRadius: random3 * 10,
      ease: Elastic.easeOut.config(0.7, 0.3),
    });
  };

  useEffect(() => {
    animateShapes();
    animateInterval.current = setInterval(() => {
      animateShapes();
    }, 2500);

    return () => clearInterval(animateInterval.current);
  }, []);

  return (
    <IntroWrapper data-scroll data-scroll-call="animateIntro">
      <Wrapper>
        <CommonTitle data-shadow="MW">Hi I'm Mason Wong</CommonTitle>
        <h3 className={`intro ${appContext.animateIntro ? "show" : ""}`}>
          <div className="intro-text">Lead UI/UX Developer</div>
          <div className="intro-text">with a passion of</div>
          <div className="intro-text">creating meaningful and</div>
          <div className="intro-text">reusable components</div>
        </h3>
      </Wrapper>
      <div className="animate-square">
        <div className="square" ref={squareRef} />
        <div className="circle" ref={circleRef} />
        <div className="rectangle" ref={rectangleRef} />
      </div>
    </IntroWrapper>
  );
};
export default Intro;
